import React, { useState, useEffect } from "react";

export default function Section3(props) {
  const [state, setState] = useState({
    organizationName1: props.state.organizationName1,
    titleOfRole1: props.state.titleOfRole1,
    workedFrom1: props.state.workedFrom1,
    workedTill1: props.state.workedTill1,

    organizationName2: props.state.organizationName2,
    titleOfRole2: props.state.titleOfRole2,
    workedFrom2: props.state.workedFrom2,
    workedTill2: props.state.workedTill2,
  });
  useEffect(() => {
    setState(props.state);
  }, [props.state]);

  return (
    <div className="section section3">
      <div className="job1">
        <label htmlFor="organizationName1">
          Name of the organization
          <input
            required
            id="organizationName1"
            name="organizationName1"
            onChange={props.onChange}
            value={state.organizationName1}
            type="text"
          />
        </label>
        <label htmlFor="titleOfRole1">
          Title of the role
          <input
            required
            id="titleOfRole1"
            name="titleOfRole1"
            onChange={props.onChange}
            value={state.titleOfRole1}
            type="text"
          />
        </label>
        <label htmlFor="workedFrom1">
          From
          <input
            required
            id="workedFrom1"
            name="workedFrom1"
            onChange={props.onChange}
            value={state.workedFrom1}
            type="date"
          />
        </label>
        <label htmlFor="workedTill1">
          To
          <input
            required
            id="workedTill1"
            name="workedTill1"
            onChange={props.onChange}
            value={state.workedTill1}
            type="date"
          />
        </label>
      </div>
      <div className="job2">
        <label htmlFor="organizationName2">
          Name of the organization
          <input
            id="organizationName2"
            name="organizationName2"
            onChange={props.onChange}
            value={state.organizationName2}
            type="text"
          />
        </label>
        <label htmlFor="titleOfRole2">
          Title of the role
          <input
            id="titleOfRole2"
            name="titleOfRole2"
            onChange={props.onChange}
            value={state.titleOfRole2}
            type="text"
          />
        </label>
        <label htmlFor="workedFrom2">
          From
          <input
            id="workedFrom2"
            name="workedFrom2"
            onChange={props.onChange}
            value={state.workedFrom2}
            type="date"
          />
        </label>
        <label htmlFor="workedTill2">
          To
          <input
            id="workedTill2"
            name="workedTill2"
            onChange={props.onChange}
            value={state.workedTill2}
            type="date"
          />
        </label>
      </div>
    </div>
  );
}
